.exp-container .left-exp p{
    padding-left: 0;
}

@media screen and (max-width: 600px) {
    header{
        padding: 0 !important;
    }
    .theme-btn{
        width: 50px;
        height: 50px;
    }
    .header-content{
        grid-template-columns: repeat(1, 1fr);
        padding-bottom: 6rem;
    }

    .left-header{
        width: 0% !important;
        .h-shape{
            display: none;
        }
    }
    .right-header{
        grid-row: 1;
        padding-right: 0rem !important;
        width: 90%;
        margin: 0 auto;
        .name{
            font-size: 2.5rem !important;
            text-align: center;
            padding-top: 3rem;
        }
        .about-me {
            display: flex;
            align-self: center;
        }
    }
    .btn-con{
        align-self: center;
    }
    .header-content .left-header .image{
        margin: 0 auto;
        width: 90%;
    }

    .controls{
        top: auto;
        bottom: 0;
        flex-direction: row;
        justify-content: center;
        left: 50%;
        transform: translateX(-50%);
        width: 100%;
        background-color: var(--color-grey-5);
        .control{
            margin: 1rem .3rem;
        }
    }

    .exp-container{
        grid-template-columns: repeat(1, 1fr);
        .right-exp{
            grid-template-columns: repeat(1, 1fr);
            padding-top: 2.5rem;
        }
        .left-exp{
            padding-right: 0;
            p{
                padding-left: 0;
            }
        }
    }

    .timeline{
        grid-template-columns: repeat(1, 1fr);
        padding-bottom: 6rem;
    }

    .container{
        //padding: 2rem 2.5rem !important;
    }

    .exp-stats{
        .progress-bars{
            grid-template-columns: repeat(1, 1fr);
        }
    }

    .projects{
        grid-template-columns: repeat(1, 1fr);
        padding-bottom: 6rem;
        margin-top: 1rem;
    }

    .blogs{
        grid-template-columns: repeat(1, 1fr);
        padding-bottom: 6rem;
    }

    .contact {
        margin-bottom: 7rem;
    }

    .contact-content-con{
        flex-direction: column;
        .right-contact{
            margin-left: 0;
            margin-top: 2.5rem;
        }
        
    }
    .contact-content-con .right-contact .i-c-2{
        flex-direction: column;
    }
    .contact-content-con .right-contact .i-c-2 :last-child{
        margin-left: 0;
        margin-top: 1.5rem;
    }

    .contact-content-con .right-contact{
        margin-bottom: 6rem;
    }

    .contact-item{
        flex-direction: column;
        margin: 1rem 0;
        p{
            font-size: 15px;
            color: var(--color-grey-2);
        }
        span{
            font-size: 15px;
        }
        .icon{
            grid-template-columns: 25px 1fr;
        }
    }


    .main-title{
        h2{
            font-size: 2rem;
            span{
                font-size: 2.3rem;
            }
            .bg-text{
                font-size: 2.3rem;
            }
        }
    }
}

@media screen and (max-width:1432px){
    .container{
        display: block;
       //padding: 7rem 11rem;
    }

    .contact-content-con{
        flex-direction: column;
        .right-contact{
            margin-left: 0;
            margin-top: 2.5rem;
        }
        
    }
    .contact-content-con .right-contact .i-c-2{
        flex-direction: column;
    }
    .contact-content-con .right-contact .i-c-2 :last-child{
        margin-left: 0;
        margin-top: 1.5rem;
    }

    .contact-content-con .right-contact{
        margin-bottom: 6rem;
    }

    .main-title{
        h2{
            .bg-text{
                font-size: 5.5rem;
            }
        }
    }
    
}

@media screen and (max-width:1300px) {
    .exp-container{
        grid-template-columns: repeat(1, 1fr);
        .right-exp{
            padding-top: 2.5rem;
            .abt-text{
                min-height: fit-content !important;
            }
        }
    } 
}




@media screen and (max-width:1250px){
    .projects{
        flex-wrap: wrap;
    }
    .header-content .right-header{
        padding-right: 9rem;
    }

}
@media screen and (max-width:660px){
    .contact-icons {
        display: grid !important;
        grid-template-columns: repeat(2, 1fr) !important;
        grid-gap: 2rem;
    }
}

@media screen and (max-width:1070px){
    .main-title{
        h2{
            font-size: 4rem;
            span{
                font-size: 4rem;
            }
            .bg-text{
                font-size: 4.5rem;
            }
        }
    }
}

@media screen and (max-width:1015px) {
    .exp-container .right-exp .abt-text{
        min-height: 245px !important;
    }
    
}

@media screen and (max-width:970px){
    .exp-container .left-exp{
        padding-right: 0rem;
    }
    .header-content{
        grid-template-columns: repeat(1, 1fr);
        padding-bottom: 6rem;
    }

    /*.left-header{
        .h-shape{
            display: none;
        }
    }*/
    .right-header{
        grid-row: 1;
        padding-right: 0rem !important;
        width: 90%;
        margin: 0 auto;
        .name{
            font-size: 2.5rem !important;
            //text-align: center;
            padding-top: 3rem;
        }
    }
}
@media screen and (max-width:700px){
    .exp-stats .progress-bars{
        grid-template-columns: repeat(1, 1fr);
    }
    .exp-container {
        .left-exp .skills-grid {
            grid-template-columns: repeat(3, 1fr);
        }
        .right-exp{
            grid-template-columns: repeat(1, 1fr);
            .abt-text {
                min-height: max-content !important;
            }
        }
    }

    .timeline{
        grid-template-columns: repeat(1, 1fr);
    }
    .main-title{
        h2{
            font-size: 3rem;
            span{
                font-size: 3rem;
            }
            .bg-text{
                font-size: 4rem;
            }
        }
    }
}

@media screen and (max-width:520px) {
    .exp-container .right-exp .abt-text {
        min-height: 245px !important;
    }
    .exp-container .left-exp .skills-grid {
        grid-template-columns: repeat(2, 1fr);
    }
}