@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
}

html {
    scroll-behavior: smooth;
}

/*
--color-primary: #28272c;
    --color-secondary: #94469b;
    --color-white: #FEFBF6;
    --color-black: #000;
    --color-grey0: #f8f8f8;
    --color-grey-1: #dbe1e8;
    --color-grey-2: #b2becd;
    --color-grey-3: #6c7983;
    --color-grey-4: #7596a7;
    --color-grey-5: #2a2e35;
    --color-grey-6: #12181b;
*/

:root {
    --color-primary: #352136;
    --color-secondary: #FFC18E;
    --color-white: #FEFBF6;
    --color-black: #000;
    --color-grey0: #f8f8f8;
    --color-grey-1: #dbe1e8;
    --color-grey-2: #b2becd;
    --color-grey-3: #7A4069;
    --color-grey-4: #CA4E79;
    --color-grey-5: #402741;
    --color-grey-6: #12181b;
    --br-sm-2: 14px;
    --box-shadow-1: 0 3px 15px rgba(0,0,0,.3);
}

body {
    background-color: var(--color-primary);
    font-family: 'Poppins', sans-serif;
    font-size: 1.1rem;
    color: var(--color-white);
    transition: all .4s ease-in-out;
}

a {
    display: inline-block;
    text-decoration: none;
    color: inherit;
    font-family: inherit;
}

main {
    min-height: 100vh;
    color: var(--color-white);
    overflow: hidden;
    padding: 0 !important;
}

section {
    min-height: 100vh;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    padding: 3rem 18rem;
}

.container {
    display: block;
    animation: appear 1s ease-in-out;
    transform: translateY(0) scaleY(1);
    background-color: var(--color-primary);
}

.other {
    padding: 5%;
}

//Controls
.controls {
    position: fixed;
    z-index: 10;
    top: 2%;
    width: 100%;
    justify-content: center;
    //right: 3%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    .control{
        padding: 1rem;
        cursor: pointer;
        background-color: var(--color-grey-4);
        width: 55px;
        height: 55px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 .7rem;
        box-shadow: var(--box-shadow-1) ;
        i {
            font-size: 1.2rem;
            color: var(--color-grey-2);
            pointer-events: none;
        }
    }
    .active-btn {
        background-color: var(--color-secondary);
        transition: all .4s ease-in-out;
        i { color: var(--color-white); }
    }
}

.theme-btn {
    top: 5%;
    right: 3%;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: var(--color-grey-4);
    cursor: pointer;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 3px 15px rgba(0,0,0,.3);
    transition: all .1s ease-in-out;
    &:active{ transform: translateY(-3px); }
    i{
        font-size: 1.4rem;
        color: var(--color-grey-2);
        pointer-events: none;
    }
}


/*Header-content*/
.header-content {
    display: flex;
    min-height: 100vh;
    .left-header {
        width: 50%;
        display: flex;
        align-items: center;
        position: relative;
        .h-shape {
            transition: all .4s ease-in-out;
            width: 100%;
            height: 100%;
            background-color: var(--color-secondary);
            position: absolute;
            left: 0;
            top: 0;
            z-index: -1;
            clip-path: polygon(0 0, 46% 0, 79% 100%, 0% 100%);
        }
    }

    .right-header {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-right: 18rem;
        width: 100%;
        .name {
            font-size: 3rem;
            span { color: var(--color-secondary); }
        }
        p {
            margin: 1.5rem 0;
            line-height: 2rem;
        }
        .about-me {
            margin-top: 3rem;
            text-align: justify;
            width: 85%;
            font-weight: 500;
            font-size: 1.2rem;
        }
    }
}

/*About*/
.exp-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding-top: 3.5rem;
    padding-bottom: 5rem;
    .right-exp {
        padding-top: 2.5rem;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 2rem;
        .exp-item {
            border: 1px solid var(--color-grey-4);
            border-radius: 5px;
            transition: all .4s ease-in-out;
            box-shadow: 1px 2px 15px rgba(0,0,0,.1);
            cursor: pointer;
            &:hover {
                transform: translateY(-5px);
                border: 1px solid var(--color-secondary);
                box-shadow: 1px 4px 15px rgba(0,0,0,.32);
            }
            .abt-text {
                min-width: 310px;
                min-height: 245px;
                padding: 1.5rem;
                display: flex;
                flex-direction: column;
                .large-text {
                    font-size: 2.4rem;
                    font-weight: 700;
                    color: var(--color-secondary);
                }
                .small-text {
                    padding-left: 3rem;
                    position: relative;
                    text-transform: uppercase;
                    font-size: 1.2rem;
                    color: var(--color-grey-1);
                    letter-spacing: 2px;
                    font-weight: 500;
                    &::before {
                        content: '';
                        position: absolute;
                        left: 0;
                        top: 15px;
                        width: 2rem;
                        height: 2px;
                        background-color: var(--color-grey-4);
                    }
                }
                .dates {
                    color: var(--color-grey-1);
                }
            }
        }
    }

    .left-exp {
        padding-right: 5rem;
        .skills-grid {
            padding-top: 1rem;
            display: grid;
            grid-template-columns: repeat(5, 1fr);
            grid-gap: 1rem;
            p {
                font-weight: 550;
                line-height: 2rem;
                padding: 0.5rem;
                color: var(--color-grey-1);
                border: 1px solid var(--color-grey-3);
                border-radius: 5px;
                text-align: center;

                &:hover {
                    cursor: default;
                    transform: translateY(-1px);
                    box-shadow: 1px 4px 15px rgba(0,0,0,.32);
                    background-color: var(--color-grey-3);
                }
            }
        }
        h2 {
            padding-top: 2rem;
            color:  var(--color-secondary);
        }
        h4 {
            font-size: 2rem;
            text-transform: uppercase;
        }

    }
}

//projects
.port-text {
    padding: 2rem 0;
    text-align: center;
}

.projects {
    display: flex;
    margin-top: 3rem;
    //flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    row-gap: 2rem;
    column-gap: 2rem;
    .project-item {
        min-width: 310px;
        max-width: 310px;
        min-height: 400px;
        border: 1px solid var(--color-grey-5);
        border-radius: 5px;
        transition: all .4s ease-in-out;
        box-shadow: 1px 2px 15px rgba(0,0,0,.1);
        background-color: var(--color-grey-5);
        cursor: pointer;
        &:hover {
            transform: translateY(-5px);
            border: 1px solid var(--color-secondary);
            box-shadow: 1px 4px 15px rgba(0,0,0,.32);
        }
        .abt-text {
            padding: 1.5rem;
            display: flex;
            flex-direction: column;
            text-align: justify;
            line-height: 1.5;
            font-weight: 500;
            .large-text {
                font-size: 2rem;
                font-weight: 700;
                color: var(--color-secondary);
                text-align: center;
                padding-bottom: 1px;
                border-bottom: 2px solid var(--color-grey-3);
            }
            .small-text {
                font-weight: 550;
                text-align: center;
                font-size: 1.2rem;
                color: var(--color-grey-1);                
                font-style: italic;
                padding: 0.5rem 0;

            }
            .dates {
                padding-top: 00.5rem;
                color: var(--color-grey-2);
                text-align: right;
                font-size: 1rem;
            }
        }
    }
}

.portfolio-item:hover .hover-items {
    opacity: 1;
    transform: scale(1);
}


//Contact section
.contact-container {
    display: flex;
    flex-direction: column;
    padding: 3.5rem;
    align-content: center;
    .main-title {
        margin-bottom: 3rem;
    }
    .contact-icons {
        display: grid;
        grid-template-columns: repeat(4, 1fr);

        .contact-icon {
            display: flex;
            flex-direction: column;
            text-align: center;
            font-weight: 550;
            
            :first-child {
                margin-left: 0 !important; 
                margin-right: 0 !important;
            }
            span {
                display: flex;
                margin-bottom: 1rem;
                background-color: var(--color-grey-3);
                width: 55px;
                height: 55px;
                border-radius: 50%;
                justify-content: center;
                align-items: center;
                align-self: center;
            }
        }
    }
    

    
}


/*Independed components*/
.btn-con {
    display: flex;
    align-self: flex-start;
}
.main-btn {
    border-radius: 30px;
    color: inherit;
    font-weight: 600;
    position: relative;
    border: 1px solid var(--color-secondary);
    display: flex;
    align-self: flex-start;
    align-items: center;
    overflow: hidden;
    .btn-text { padding: 0 2rem; }
    .btn-icon {
        background-color: var(--color-secondary);
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        padding: 1rem;
    }
    &::before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        transform: translateX(100%);
        transition: all .4s ease-out;
        z-index: -1;
    }
    &:hover {
        transition: all .4s ease-out;
        &::before {
            width: 100%;
            height: 100%;
            background-color: var(--color-secondary);
            transform: translateX(0);
            transition: all .4s ease-out;
        }
    }

}

.main-title {
    text-align: center;
    h2 {
        position: relative;
        text-transform: uppercase;
        font-size: 4rem;
        font-weight: 700;
        span { color: var(--color-secondary); }
        .bg-text {
            position: absolute;
            top: 50%;
            left: 50%;
            color: var(--color-grey-4);
            transition: all .4s ease-in-out;
            z-index: -1;
            transform: translate(-50%, -50%);
            font-weight: 800;
            font-size: 6.3rem;
        }
    }
}

@import './media';